:root {

/* Colors: */
--dark-blu: #1B2B5E;
--medium-blu: #293E83;
--light-blu: #3550AF;
--white-blu: #F4F8FB;
--yellow: #FFBC00;
--light-sand: #F7F7F2;
--grey: #E5E5E5;
--titles: 'DM Serif Display', serif;
--bodys: 'Raleway', sans-serif;
--box-shadow: 0px 0px 20px rgba(186,182,164,0.3);
--radial-blu-gradient: radial-gradient( #253979 , #0D1A45);
}


html,body {
  margin:0;
  padding:0;
  font-family: var(--bodys);
  font-size:16px;
  line-height: 1.9em;
  text-rendering: geometricPrecision;
}

h1,h2,h3,h4,h5, .fake-title {
  font-family:var(--titles);
  font-weight:300;
}


h2 {
  font-size:46px;
  line-height:1em;
}

a {
  text-decoration: none;
  transition-duration:.2s;
  -moz-transition-duration:.2s;
  -webkit-transition-duration:.2s;
}

.p-small {
  font-size:13px;
}



.simple-button-yellow-arrow,
.simple-button-yellow {
  font-weight:700;
  text-decoration: none;
  color:var(--yellow);
  position:relative;
}

.simple-button-yellow-arrow::after {
  content:"";
  width:6px;
  height:12px;
  background-image:url("../img/simple-yellow-arrow.svg");
  background-size:6px;
  position:relative;
  display: inline-block;
  margin-left:10px;
  top:1px;
  transition-duration: .2s;
  -moz-transition-duration: .2s;
  -webkit-transition-duration: .2s;
}

.simple-button-yellow-arrow:hover::after {
  margin-left:18px;
}
